@charset "utf-8";
@import "../includes/zindex";

// main content
.spop-bnr {
  position: relative;
  margin-top: 4vw;
  font-size: 0;
  a {
    display: block;
  }
  img {
    width: 100%;
  }
  .spop-close {
    position: absolute;
    right: 4.8vw;
    top: 50%;
    width: 6.133vw;
    height: 6.133vw;
    background: url('/_ui/img/m/icn/icn-x.png') no-repeat;
    background-size: 100%;
    font-size: 0;
    text-indent: -999px;
    -webkit-transform: translateY(-50%);
    -moz-transform: translateY(-50%);
    -o-transform: translateY(-50%);
    -ms-transform: translateY(-50%);
    transform: translateY(-50%);
  }
}
.list1-prod + .prod-big-bnr {
  .prod-list-top {
    margin-top: 15.333vw;
  }
}
.list2-prod + .prod-big-bnr {
  .prod-list-top {
    margin-top: 15.333vw;
  }
}
.prod-list-top.no-title {
  margin-top: 1.333vw;
}
.bnr-more {
  width: 7.733vw;
  height: 7.733vw;
  display: block;
  background: url('/_ui/img/m/icn/icn-more.png') no-repeat;
  background-size: 100%;
  font-size: 0;
  text-indent: -999px;
  margin: 6.666vw auto;
}
// .prod-list-top.deals {
//   margin-top: 14.666vw;
// }
.list1-prod>ul {
  background: #f6f6f6;
  li {
    overflow: hidden;
    margin-top: 2.4vw;
    background: #fff;
    &:first-child {
      margin-top: 0;
      padding-top: 2.4vw;
    }
    a {
      display: block;
    }
    .prod-img {
      width: 48vw;
      height: 71.733vw;
      float: right;
      overflow: hidden;
      position: relative;
      img {
        width: 100%;
        height: auto;
        position: absolute;
        top: 0;
        left: 0;
      }
    }
    .txt-box {
      float: left;
      width: 40vw;
      color: #1f1f1f;
      margin: 8.266vw 0 7.733vw;
      height: 56.266vw;
      position:  relative;
      padding-left: 4.8vw;
      span {
        display: block;
      }
      .brand {
        display: inline-block;
        font-size: 3.333vw;
        font-weight: 700;
        border-bottom: .8vw #1f1f1f solid;
        padding-bottom: .666vw;
        margin-bottom: 3.733vw;
      }
      .tit {
        font-size: 3.466vw;
        line-height: 4.666vw;
        font-weight:  700;
        letter-spacing: -.133vw;
      }
      .btm {
        position: absolute;
        bottom: 0;
      }
      .discount {
        font-size: 3.466vw;
        font-weight: 700;
        margin-bottom: .8vw;
        background: url('/_ui/img/m/icn/icn-sale.png') no-repeat left center;
        padding-left: 3.333vw;
        background-size: auto 2.4vw;
      }
      .price {
        font-size: 3.6vw;
        margin-bottom: .533vw;
        i {
          color: #9ba4b0;
          text-decoration: line-through;
          margin-right: 2.666vw;
        }
      }
    }
  }
}
.list2-prod .bnr-more {
  margin: 5.333vw auto;
}
.btm-bnr li {
  margin-top: 2.133vw;

  img {
    width: 100%;
  }
}
.scr-prod {
  margin: 6.8vw auto 12vw;
}
// setting page
.main {
  header, .header-padding, .scr-navi, .icn-navi {
    transition: .2s cubic-bezier(.25,.8,.4,.95);
  }
  .scr-navi {
    height: 15.6vw;
    background: #fff;
    border-bottom: 0.266vw solid rgba(0, 0, 0, 0.07);
    .icn-navi.right {
      left: 79.6vw;
      right: inherit;
    }
  }
  &.head-fix {
    #kolon-content {
      margin-top: 15.866vw;
    }
    .scr-navi {
      position: fixed;
      z-index: 100;
      top: 14.666vw;
      width: 100%;
      box-sizing: border-box;
    }
  }
  &.head-hide {
    header {
      top: -14.666vw;
    }
    .scr-navi {
      top: 0;
    }
  }
}
#kolon-content.setting {
  background: #f7f7f7;
  margin-top: 0;
  .user {
    overflow: hidden;
    position: relative;
    margin-top: 0;
    padding: 0 7.466vw 4.66vw;
    background: #fff;
    span {
      font-size: 4vw;
      color: #1f1f1f;
      line-height: 7.733vw;
    }
    .btn-login {
      float: right;
      width: 18.133vw;
      height: 7.733vw;
      font-size: 3.733vw;
      color: #1f1f1f;
      line-height: 7.733vw;
      text-align: center;
      border: .266vw solid #f1f1f1;
    }
  }
  .login {
    border-bottom: .266vw solid #f1f1f1;
    span {
      font-weight: 700;
    }
  }
  .chk-auto {
    padding: 5.6vw 7.466vw;
    background: #fff;
    p {
      display: inline-block;
      font-size: 3.733vw;
      color: #1f1f1f;
    }
  }
  .notice {
    margin-top: 2.133vw;
    background: #fff;
    .box-txt {
      padding: 6.66vw 7.466vw;
      border-bottom: .266vw solid #f7f7f7;
      h4 {
        margin-bottom: 8vw;
        font-size: 4vw;
        font-weight: bold;
        color: #1f1f1f;
      }
      p {
        font-size: 3.733vw;
      }
      .is-agr {
        display: inline-block;
        margin-bottom: 4.8vw;
        color: #1f1f1f;
        .agr-on {
          display: block;
          margin-top: 1.066vw;
          font-size: 3.2vw;
          color: #aaa;
        }
      }
      .txt {
        color: #898989;
        line-height: 4.8vw;
      }
      .vsn {
        span {
          float: right;
          color: #898989;
        }
      }
      .btn-view {
        display: inline-block;
        padding-top: 8.266vw;
        font-size: 3.466vw;
        color: #898989;
        text-decoration: underline;
      }
      .popup {
        visibility: hidden;
        position: fixed;
        width: 96%;
        max-width: 740px;
        left: 50%;
        bottom: 5%;
        padding: 5.33vw 0;
        background: #fff;
        text-align: center;
        box-sizing: border-box;
        box-shadow: 0 0 150px rgba(0, 0, 0, .1);
        transform: translateX(-50%);
        z-index: $mini-pop;
        h5 {
          font-size: 3.733vw;
          color: #1f1f1f;
        }
        .date {
          margin-top: 1.866vw;
          font-size: 3.2vw;
          color: #aaa;
        }
        .pop-txt {
          margin-top: 3.733vw;
          font-size: 3.466vw;
          color: #898989;
        }
      }
      .popup.show {
        visibility: visible;
        -webkit-animation: fadein .5s, fadeout .5s 2.5s;
        animation: fadein .5s, fadeout .5s 2.5s;
      }
    }
    .box-btn {
      a {
        position: relative;
        display: block;
        padding: 6.133vw 7.466vw;
        font-size: 3.733vw;
        color: #1f1f1f;
        &::after {
          content: '';
          float: right;
          width: 3.466vw;
          height: 2vw;
          margin-top: 1.2vw;
          background: url('/_ui/img/m/icn/icn-arrow04.png') no-repeat;
          background-size: contain;
          transform: rotate(-90deg);
          -webkit-transform: rotate(-90deg);
          -ms-transform: rotate(-90deg);
          -moz-transform: rotate(-90deg);
        }
        span {
          display: inline-block;
          position: absolute;
          top: 6.133vw;
          right: 12vw;
        }
      }
      .type01 {
        border-bottom: .266vw solid #f7f7f7;
      }
    }
  }
}
.tg-btn {
  display: block;
  float: right;
  width: 11.2vw;
  height: 6.4vw;
  background: url('/_ui/img/m/icn/icn-tg-off.png') no-repeat;
  background-size: 100%;
  font-size: 0;
  text-indent: -9999px;
}
.tg-btn.on {
  background: url('/_ui/img/m/icn/icn-tg-on.png') no-repeat;
  background-size: 100%;
}

// menu
.kolon-menu {
  max-width: 750px;
  min-width: 320px;
  box-sizing: border-box;
  .gnb {
    overflow: hidden;
    padding: 0 4.8vw;
    color: #fff;
    .my-info {
      float: left;
      display: table;
      width: 57.4vw;
      height: 14.666vw;
      .user-log {
        font-size: 0;
        overflow: hidden;
        display: table-cell;
        vertical-align: middle;
        & > * {
          vertical-align: middle;
        }
        .name {
          position: relative;
          display: inline-block;
          font-size: 4.266vw;
          color: #1f1f1f;
          strong {
            color: #1f1f1f;
            border-bottom: 0.266vw solid #1f1f1f;
          }
        }
        u {
          text-decoration: none;
          border-bottom: 0.266vw solid #1f1f1f;
        }
        .separater {
          display: inline-block;
          border-left: .266vw solid #d2d2d2;
          margin: 0 2vw;
          height: 3.5vw;
        }
        .bt-login {
          word-break: keep-all;
          font-size: 4.266vw;
          color: #1f1f1f;
        }
      }
    }
    ul {
      float: right;
      overflow: hidden;
      width: 33vw;
      text-align: right;
      white-space: nowrap;
      li {
        display: inline-block;
        position: relative;
        width: 10.7vw;
        height: 14.666vw;
        &:first-child {
          margin-left: 0;
        }
        a {
          display: block;
          float: left;
          font-size: 0;
          text-indent: -9999px;
          transform: translateY(50%);
          padding: 2vw;
        }
        .mypage {
          width: 8vw;
          height: 8vw;
          margin-top: -4.8vw;
          background: url('/_ui/img/m/icn/icn-mypage.png') no-repeat center center;
          background-size: 6.533vw;
        }
        .my-alert {
          width: 8.133vw;
          height: 8.933vw;
          margin-top: -5.6vw;
          font-size: 4.266vw;
          line-height: 8.933vw;
          text-indent: 0;
          color: #1f1f1f;
          i {
            position: absolute;
            display: block;
            font-size: 2.933vw;
            color: #fff;
            font-family: 'helvetica';
            font-weight: 700;
            width: 4.533vw;
            height: 4.533vw;
            border-radius: 50%;
            background: #f23d4e;
            line-height: 4.533vw;
            text-align: center;
            text-indent: 0;
            top: 1.4666vw;
            left: 8.666vw;
          }
        }
        .close {
          width: 8.266vw;
          height: 8.266vw;
          margin-top: -5vw;
          background: url('/_ui/img/m/icn/icn-close3.png') no-repeat center center;
          background-size: 4.266vw;
        }
      }
    }
  }
  nav {
    margin-top: 5.066vw;
    padding-bottom: 13.8584vw;
    .main-menu {
      margin-left: 4.933vw;
      padding-bottom: 3.466vw;
      li {
        line-height: 1;
        border-bottom: .266vw solid #f3f3f3;
        &:last-child {
          border-bottom: 0;
        }
        &>a {
          display: block;
          padding: 5.066vw 0 5.066vw 2vw;
          font-family: "Noto Sans KR";
          font-size: 3.733vw;
          font-weight: 500;
          color: #1f1f1f;
        }
        &.home {
          &>a {
            font-family: 'helvetica';
            font-weight: 900;
          }
        }
        &.depth {
          a {
            font-family: 'helvetica';
          }
          &>a {
            font-weight: 700;
            background: url('/_ui/img/m/icn/icn-plus2.png') no-repeat 92% center;
            background-size: 3.2vw;
          }
          &.on {
            &>a {
              background: url('/_ui/img/m/icn/icn-minus3.png') no-repeat 92% center;
              background-size: 3.2vw;
            }
          }
          .depth-02 {
            display: none;
            padding: 3.33vw 0 3.33vw 2.666vw;
            border-top: .266vw solid #f3f3f3;
            li {
              a {
                color: #898989;
              }
            }
            .all {
              a {
                background: url('/_ui/img/m/icn/icn-arrow09.png') no-repeat 91% center;
                background-size: 1.733vw;
              }
            }
            .hs-sub {
              a {
                background: url('/_ui/img/m/icn/icn-more5.png') no-repeat 92% center;
                background-size: 3.2vw;
              }
              &.on {
                >a {
                  color: #1f1f1f;
                  background: url('/_ui/img/m/icn/icn-minus.png') no-repeat 92% center;
                  background-size: 3.2vw;
                }
              }
            }
          }
          .depth-03 {
            display: none;
            padding: 4.8vw 0 7.466vw 2vw;
            border-top: .266vw solid #f3f3f3;
            li {
              border-bottom: 0;
              a {
                padding: 3.466vw 0;
                color: #aaa;
                background: none !important;
              }
            }
          }
        }
        &.highlight {
          &>a {
            font-family: "Noto Sans KR";
            color: #3d84e5;
          }
        }
      }
    }
    .sub-menu {
      background: #f8f8f8;
      li {
        line-height: .8;
        a {
          display: block;
        }
      }
      .member {
        margin-left: 6.933vw;
        padding: 3.466vw 0;
        a {
          display: block;
          padding: 4.933vw 0;
          font-size: 3.733vw;
          color: #1f1f1f;
        }
        .setting {
          border-bottom: .266vw solid #f0f0f0;
        }
      }
    }

    .footer-menu {
      background: #fff;
      position: fixed;
      left: 0;
      top: 100%;
      bottom: 0;
      margin-top: -12.82vw;
      width: 100%;
      transition: 0.3s cubic-bezier(0.25, 0.8, 0.4, 0.95);
      &.on {
        top: 0;
        margin-top: 0;
      }
    }

    .brand-list {
      background: #fff;
      .brand-open {
        display: block;
        height: 12.82vw;
        line-height: 12.82vw;
        padding: 0 6.933vw;
        background: #fff url('/_ui/img/m/icn/icn-arrow-open.png') no-repeat 92% center;
        background-size: 2.8vw;
        font-family: 'helvetica';
        font-size: 4.133vw;
        font-weight: 700;
        color: #1f1f1f;
        border-top: .266vw solid rgba(0, 0, 0, .07);
      }
      &.on {
        .brand-open {
          background: #fff url('/_ui/img/m/icn/icn-arrow-close.png') no-repeat 92% center;
          background-size: 2.8vw;
        }
      }
      .tabs {
        display: none;
        height: 12.82vw;
        margin-top: 0.933vw;
        padding: 0 4.933vw;
        line-height: 12.82vw;
        border-bottom: .266vw solid #ededed;
        font-size: 0;
        .brand-tab {
          font-size: 4vw;
          color: #ccc;
          font-weight: bold;
          margin-right: 5.066vw;
          display: inline-block;
          padding: 2.933vw 2vw 2vw;
          line-height: 1.5;
          &.active {
            color: #1f1f1f;
          }
        }
      }
      .list {
        display: none;
        padding: 6.666vw 6.933vw 9.466vw 4.933vw;
        position: absolute;
        top: 27.066vw;
        left: 0;
        width: 100%;
        bottom: 0;
        overflow: auto;
        box-sizing: border-box;
        -webkit-overflow-scrolling: touch;
        &.active {
          display: block;
        }
        li {
          a {
            display: block;
            padding: 3.466vw 2vw;
            font-size: 3.466vw;
            color: #898989;
            &:hover {
              color: #1f1f1f;
            }
          }
        }
      }
    }
  }
}

